import React from "react";
import styles from "./Resume.module.css";
import {Skill} from "./Skill";
import {Experience} from "./Experience";
import {faCode} from "@fortawesome/free-solid-svg-icons";
import {faCog} from "@fortawesome/free-solid-svg-icons";
import {faGlobe} from "@fortawesome/free-solid-svg-icons";
import {PortfolioContext} from "../../../context/PortfolioContext";

export class Resume extends React.Component {
    static contextType = PortfolioContext;

    render() {
        let skillData = this.context.skills;
        let experienceData = this.context.experience;

        return (
            <section className={styles.resume}>
                <h1>Resume</h1>
                <section className={styles.container}>
                    <section className={styles.experience}>
                        <article className={styles.item}>
                            <h2>Work Experience​​</h2>
                            <section>
                                {experienceData ? experienceData["work"].map((data,i) => (
                                    <Experience index={i}
                                        job={data.job}
                                        company={data.company}
                                        time={data.time}
                                        description={data.description}
                                    />
                                )) : false}
                            </section>
                        </article>
                        <article className={styles.item}>
                            <h2>Education</h2>
                            <section>
                                {experienceData ? experienceData["education"].map((data,i) => (
                                    <Experience index={i}
                                        job={data.job}
                                        company={data.company}
                                        time={data.time}
                                        description={data.description}
                                    />
                                )) : false}
                            </section>
                        </article>
                    </section>
                    <section className={styles.skill}>
                        <Skill icon={faCode} type="Coding" skillData={skillData ? skillData["Coding"] : false}/>
                        <Skill
                            icon={faCog}
                            type="Soft Skills"
                            skillData={skillData ? skillData["Soft Skills"] : false}
                        />
                        <Skill
                            icon={faGlobe}
                            type="Language"
                            skillData={skillData ? skillData["Language"] : false}
                        />
                    </section>
                </section>
            </section>
        );
    }
}
