import React from "react";
import styles from "./ProjectDetail.module.css";

export const ProjectDetail = props => {
  const { name, thumbnail, description,url } = props.project;
  const projectNo = props.no.toString().padStart(2, "0");

  return (
    <section className={styles.item} >
      <div className={styles.thumbnail} key={ props.no}>
          <a href={thumbnail.url} title={thumbnail.name}  target={'_blank'}>
              <img src={process.env.PUBLIC_URL +'/images/' +thumbnail.src} alt={thumbnail.alt}/>
          </a>

      </div>
      <section className={styles.detail} >
        <p className={styles.projectNo}>Project | {projectNo}</p>
        <p className={styles.projectName}>{name}</p>
        <div className={styles.projectDes}  key={ props.no} >
          {description.map((line,i) => (
            <p key={i}>{line}</p>
          ))}
        </div>
      </section>
    </section>
  );
};
